import Logo from '../images/logo.png';


function Footer(){
    return(
        <div id="footer">
            <div className='footer_con'>
                <div className='info'>
                    <figure>
                        <img src={Logo}></img>
                    </figure>
                    <div className='company_info'>Re-shaping the asset<br/> management industry.</div>
                    
                </div>
                <div className='etc'>
                    <div className='company'>2022 by LINE INVESTMENT TECHNOLOGY.</div>
                </div>
            </div>
        </div>
    )
}

export default Footer;