import React, { useLayoutEffect, useState } from "react";
import ParticleImage, { forces, Vector } from "react-particle-image";
import Particle from "../images/particle.png";

var src = Particle;
function debounce(fn, ms) {
  let timer;
  return () => {
    clearTimeout(timer);
    timer = setTimeout((_) => {
      timer = null;
      fn.apply(this, arguments);
    }, 100);
  };
}

function useWindowSize() {
  const [size, setSize] = useState([0, 0]);
  useLayoutEffect(() => {
    function updateSize() {
      let copy = size;
      copy = [window.innerWidth, window.innerHeight];
      setSize(copy);
    }
    window.addEventListener("resize", updateSize);
    updateSize();
    return () => window.removeEventListener("resize", updateSize);
  }, []);

  return size;
}

export default function Party(props) {
  const [width, height] = useWindowSize();

  const [dimensions, setDimensions] = React.useState({
    height: window.innerHeight,
    width: window.innerWidth,
  });
  React.useEffect(() => {
    const debouncedHandleResize = debounce(function handleResize() {
      setDimensions({
        height: window.innerHeight,
        width: window.innerWidth,
      });
    }, 1000);
    window.addEventListener("resize", debouncedHandleResize);

    return (_) => {
      window.removeEventListener("resize", debouncedHandleResize);
    };
  });

  return (
    <div
      id="particle"
      className="width_con"
      style={{ height: "100vh", width: "100%" }}
    >
      <ParticleImage
        scale={props.scale} // 
        width={width}
        height={height}
        entropy={2}
        maxParticles={props.maxParticles} //
        backgroundColor="rgba(31, 31, 31,0)" // 
        src={src} //
        mouseMoveForce={(x, y) => forces.disturbance(x, y, 10)} // 
        touchMoveForce={(x, y) => forces.disturbance(x, y, 5)}
        mouseDownForce={(x, y) => forces.disturbance(x, y, 500)} // 
        particleOptions={{
          mass: () => 10,
          filter: ({ x, y, image }) => {
            const pixel = image.get(x, y);
            return pixel.r === 255; //
          },
          color: getRandomColor, // 
          friction: () => 0.7, //  
          radius: () => 1, //dot 
          initialPosition: ({ canvasDimensions }) => {
            return new Vector(
              canvasDimensions.width / 2,
              canvasDimensions.height / 2
            );
          },
        }}
      />
    </div>
  );
}

function getRandomColor() {
  return "#" + Math.floor(Math.random() * 16777215).toString(16);
}
