import React, { useCallback, useEffect, useState } from "react";
import { Link, useLocation } from "react-router-dom";
import styled from "styled-components";
import Logo from "../images/header_logo.png";
import lang from "../images/lang.png";

const SLink = styled(Link)`
  text-decoration: none;
  margin-left: 60px;
  position: relative;
  color: ${(props) =>
    props.test == "/Approach" || props.test == "/Contact"
      ? props.$current
        ? "var(--key-color)"
        : "#000"
      : props.$current
      ? "var(--key-color)"
      : "#fff"};
  @media screen and (max-width: 1440px) {
    color: ${(props) => (props.$current ? "var(--key-color)" : "#7b7b7b")};
  }
  font-size: 18px;
  font-weight: bold;
`;

const Sspan = styled.span`
  display: block;
  z-index: -1;
  height: 4px;
  position: absolute;
  top: 50%;
  left: 0;
  width: 32px;
  margin-left: 4px;
  transition: all 1s ease-in;
`;

export const WithRouterSample = () => {
  const location = useLocation();

  const [reverse, setReverse] = React.useState(false);
  const removeClass = () => {
    setReverse(false);
  };
  const addClass = () => {
    setReverse(true);
  };

  const [on, setOn] = useState(false);
  const toggleMenu = () => {
    setOn((on) => !on);
  };

  const [isOpen, setMenu] = useState(false); //   false 

  const toggle = () => {
    setMenu((isOpen) => !isOpen); // on,off  boolean
  };

  const [y, setY] = useState(document.scrollingElement.scrollHeight);
  const [scrollDirection, setScrollDirection] = useState(
    "you have not scrolled yet"
  );

  const handleNavigation = useCallback(
    (e) => {
      if (y > window.scrollY) {
        document.getElementById("header").style.transform = "translateY(0)";
      } else if (y < window.scrollY) {
        document.getElementById("header").style.transform = "translateY(-80px)";
      }

      setY(window.scrollY);
    },
    [y]
  );

  useEffect(() => {
    window.addEventListener("scroll", handleNavigation);

    return () => {
      window.removeEventListener("scroll", handleNavigation);
    };
  }, [handleNavigation]);

  return (
    <header id="header" className={`${reverse ? "reverse" : ""}`}>
      <div className={`head  ${on ? "on" : ""}`}>
        <figure>
          <Link to="/">
            <img src={Logo}></img>
          </Link>
        </figure>

        <div className="mb_btn">
          <button type="button" onClick={() => toggle()}>
            <img src={lang}></img>
          </button>
          <nav className={isOpen ? "language" : "language hide"}>
            <ul>
              <li>
                <Link className="selected">
                  <span>EN</span>
                </Link>
              </li>
              <li>
                <Link
                  onClick={() => {
                    alert("");
                  }}
                >
                  <span>KO</span>
                </Link>
              </li>
              <li>
                <Link
                  onClick={() => {
                    alert("");
                  }}
                >
                  <span>JP</span>
                </Link>
              </li>
            </ul>
          </nav>
        </div>
      </div>
    </header>
  );
};

export default WithRouterSample;
